.recipe{
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(71, 71,71);
    margin: 20px;
    display:flex;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    align-items: center;
    min-width: 40%;
    font:white;
}
.image{
    border-radius: 25%;
    height: 105px;
    width: 150px;
}